</code></pre><div class="highlight-lines"><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="MyBehavior.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// The type of view we specify in the super class should match the type of view the behavior can be added to.</span>
<span class="token comment">// Currently it's &lt;TextView>, but it could just as well be &lt;View> in this case, since this behavior is</span>
<span class="token comment">// not dependent on any TextView specific functionality (for example, it would work equally well on an ImageView).</span>
<span class="token keyword">class</span> <span class="token function">MyBehavior</span><span class="token punctuation">(</span>context<span class="token operator">:</span> Context<span class="token punctuation">,</span> attrs<span class="token operator">:</span> AttributeSet<span class="token punctuation">)</span> <span class="token operator">:</span>
    CoordinatorLayout<span class="token punctuation">.</span>Behavior<span class="token operator">&lt;</span>TextView<span class="token operator">></span><span class="token punctuation">(</span>context<span class="token punctuation">,</span> attrs<span class="token punctuation">)</span> <span class="token punctuation">{</span>
  
  <span class="token comment">// This function will be called by the CoordinatorLayout.</span>
  <span class="token comment">// The parameter "child" is the view this behavior has been added to.</span>
  <span class="token comment">// We should return back true if we are dependent on the parameter "dependency", otherwise false.</span>
  <span class="token comment">// This function will be called once for all views in the CoordinatorLayout except the one the behavior has been added to.</span>
  <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">layoutDependsOn</span><span class="token punctuation">(</span>
    parent<span class="token operator">:</span> CoordinatorLayout<span class="token punctuation">,</span>
    child<span class="token operator">:</span> TextView<span class="token punctuation">,</span>
    dependency<span class="token operator">:</span> View
  <span class="token punctuation">)</span> <span class="token operator">=</span> dependency<span class="token punctuation">.</span>id <span class="token operator">==</span> R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>first_text_view
  
  <span class="token comment">// This function will be called whenever a view we depend on (according to layoutDependsOn())</span>
  <span class="token comment">// changes size or position. </span>
  <span class="token comment">// The parameter "child" is the view this behavior has been added to.</span>
  <span class="token comment">// The parameter "dependency" is the view that has changed size or position.</span>
  <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onDependentViewChanged</span><span class="token punctuation">(</span>
    parent<span class="token operator">:</span> CoordinatorLayout<span class="token punctuation">,</span>
    child<span class="token operator">:</span> TextView<span class="token punctuation">,</span>
    dependency<span class="token operator">:</span> View
  <span class="token punctuation">)</span><span class="token operator">:</span> Boolean <span class="token punctuation">{</span>
    
    <span class="token comment">// Set our view's left margin to the same margin as the view we depend on (the first TextView).</span>
    <span class="token keyword">val</span> childLayoutParams <span class="token operator">=</span> child<span class="token punctuation">.</span>layoutParams <span class="token keyword">as</span> ViewGroup<span class="token punctuation">.</span>MarginLayoutParams
    <span class="token keyword">val</span> dependencyLayoutParams <span class="token operator">=</span> dependency<span class="token punctuation">.</span>layoutParams <span class="token keyword">as</span> ViewGroup<span class="token punctuation">.</span>MarginLayoutParams
    childLayoutParams<span class="token punctuation">.</span>leftMargin <span class="token operator">=</span> dependencyLayoutParams<span class="token punctuation">.</span>leftMargin
    
    <span class="token comment">// Tell the framework to repaint the view.</span>
    child<span class="token punctuation">.</span><span class="token function">requestLayout</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
    
    <span class="token comment">// Return true to tell the CoordinatorLayout that we have changed this view.</span>
    <span class="token keyword">return</span> <span class="token boolean">true</span>
    
  <span class="token punctuation">}</span>
  
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br></div></div></CodeGroupItem>
</CodeGroup>
<Smartphone>
  <div>Some text</div>
  <div>Some other text</div>
  <button onclick="this.previousElementSibling.previousElementSibling.innerHTML = '&nbsp;'+this.previousElementSibling.previousElementSibling.innerHTML; this.previousElementSibling.innerHTML = '&nbsp;'+this.previousElementSibling.innerHTML">Move first text</button>
</Smartphone>
</div>
<p>Often we don't create our own behaviors (the code for behaviors usually get quite complicated), but instead use some behaviors others have created for us. The Android support library has some for different type of behaviors we can use.</p>
<h3 id="anchors" tabindex="-1"><a class="header-anchor" href="#anchors" aria-hidden="true">#</a> Anchors</h3>
<p>The <code>CoordinatorLayout</code> has one new way to position its views: using anchors. You can &quot;anchor&quot; a view to another view, and then that view will stick to that other view, even when that other view is moved.</p>
<p>Before we created our own <code>Behavior</code> to make one <code>TextView</code> follow another <code>TextView</code> as it moved. This is more or less what anchors are used for (implemented as a <code>Behavior</code>), so we could achieve similar functionality using the <code>layout_anchor*</code> attributes, as shown in the example below.</p>
<p>::::: Example</p>
<CodeGroup>
<CodeGroupItem title="res/layout/activity_main.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>androidx.coordinatorlayout.widget.CoordinatorLayout</span>
  <span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
  <span class="token attr-name"><span class="token namespace">xmlns:</span>app</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res-auto<span class="token punctuation">"</span></span>
  <span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
  <span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/first_text_view<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Some text.<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>

    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/second_text_view<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Some other text.<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">app:</span>layout_anchor</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@id/first_text_view<span class="token punctuation">"</span></span> 
      <span class="token attr-name"><span class="token namespace">app:</span>layout_anchorGravity</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>end|bottom<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span> 
      <span class="token comment">&lt;!-- Changes from before:
           1. layout_marginTop has been removed.
           1. Our own behavior has been removed.
           2. Anchor this view to the end (right in LTR) and below the first TextView (the horizontal center of this view will match the end of the first TextView) --></span>

    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>Button</span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/move_button<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>layout_marginTop</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>60dp<span class="token punctuation">"</span></span>
      <span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Move first text<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>

<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>androidx.coordinatorlayout.widget.CoordinatorLayout</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="MainActivity.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token comment">// Same as before.</span>
<span class="token keyword">class</span> MainActivity <span class="token operator">:</span> <span class="token function">AppCompatActivity</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  
  <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token operator">:</span> Bundle<span class="token operator">?</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
    <span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token punctuation">)</span>
    
    <span class="token function">setContentView</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>activity_main<span class="token punctuation">)</span>
    
    <span class="token keyword">val</span> firstTextView <span class="token operator">=</span> findViewById<span class="token operator">&lt;</span>TextView<span class="token operator">></span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>first_text_view<span class="token punctuation">)</span>
    <span class="token keyword">val</span> moveButton <span class="token operator">=</span> findViewById<span class="token operator">&lt;</span>Button<span class="token operator">></span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>move_button<span class="token punctuation">)</span>
    
    moveButton<span class="token punctuation">.</span><span class="token function">setOnClickListener</span> <span class="token punctuation">{</span>
      <span class="token keyword">val</span> layoutParams <span class="token operator">=</span> firstTextView<span class="token punctuation">.</span>layoutParams <span class="token keyword">as</span> ViewGroup<span class="token punctuation">.</span>MarginLayoutParams
      layoutParams<span class="token punctuation">.</span>leftMargin <span class="token operator">=</span> layoutParams<span class="token punctuation">.</span>leftMargin <span class="token operator">+</span> <span class="token number">5</span>
      firstTextView<span class="token punctuation">.</span><span class="token function">requestLayout</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
    
  <span class="token punctuation">}</span>
  
<span class="token punctuation">}</span>
</code></pre><div class="highlight-lines"><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br></div></div></CodeGroupItem>
